import React from "react";
import styled from "styled-components";
import { motion } from "framer-motion";

const ExperienceSection = styled.section`
  min-height: 100vh;
  padding-top: 100px;
  padding-bottom: 4rem;
`;

const Container = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
`;

const Title = styled(motion.h2)`
  font-size: 2.5rem;
  margin-bottom: 3rem;
  color: ${({ theme }) => theme.text};
`;

const Timeline = styled(motion.div)`
  position: relative;
  max-width: 800px;
  margin: 0 auto;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 2px;
    background: ${({ theme }) => theme.primary};

    @media (min-width: 768px) {
      left: 50%;
      transform: translateX(-50%);
    }
  }
`;

const TimelineItem = styled(motion.div)`
  padding: 1.5rem;
  background: ${({ theme }) => theme.cardBackground};
  border-radius: 8px;
  margin-bottom: 2rem;
  position: relative;
  margin-left: 2rem;

  @media (min-width: 768px) {
    width: calc(50% - 2rem);
    margin-left: ${({ isLeft }) => (isLeft ? "0" : "auto")};
    margin-right: ${({ isLeft }) => (isLeft ? "auto" : "0")};
  }

  &::before {
    content: "";
    position: absolute;
    left: -2.5rem;
    top: 1.5rem;
    width: 1rem;
    height: 1rem;
    background: ${({ theme }) => theme.primary};
    border-radius: 50%;

    @media (min-width: 768px) {
      left: ${({ isLeft }) => (isLeft ? "auto" : "-0.5rem")};
      right: ${({ isLeft }) => (isLeft ? "-0.5rem" : "auto")};
    }
  }
`;

const Company = styled.h3`
  font-size: 1.5rem;
  color: ${({ theme }) => theme.text};
  margin-bottom: 0.5rem;
`;

const Role = styled.h4`
  font-size: 1.2rem;
  color: ${({ theme }) => theme.primary};
  margin-bottom: 0.5rem;
`;

const Period = styled.p`
  font-size: 1rem;
  color: ${({ theme }) => theme.secondaryText};
  margin-bottom: 1rem;
`;

const Description = styled.ul`
  color: ${({ theme }) => theme.secondaryText};
  padding-left: 1.2rem;

  li {
    margin-bottom: 0.5rem;
  }
`;

const Experience = () => {
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
    },
  };

  const experiences = [
    {
      company: "Povio",
      role: "JavaScript Engineer",
      period: "Jan 2024 - Present",
      achievements: [
        "Led backend development using NestJS, improving API response times.",
        "Refactored code for better readability and type safety.",
        "Built real-time chat and collaborative editing features with Sendbird and Socket.IO.",
        "Integrated task management and third-party services like Apollo and Tiptap.",
      ],
    },
    {
      company: "Lower the Curve",
      role: "Full-stack Developer",
      period: "May 2023 - Jan 2024",
      achievements: [
        "Developed custom Shopify checkout extensions using Node.js and GraphQL.",
        "Implemented seamless payment gateway integrations.",
        "Built full-stack applications with React and PostgreSQL.",
        "Improved data management and error handling within Shopify API integrations.",
      ],
    },
    {
      company: "Radix",
      role: "Software Engineer Intern",
      period: "Jan 2023 - Apr 2023",
      achievements: [
        "Built scalable RESTful APIs with Node.js and TypeScript.",
        "Optimized database schemas and improved data fetching speeds.",
        "Developed responsive web applications using Angular.",
      ],
    },
    {
      company: "Mobile Electronics",
      role: "Mobile Phone Technician",
      period: "June 2019 - Nov 2022",
      achievements: [
        "Repaired hardware and logic board issues, including micro-soldering.",
        "Recovered lost mobile phone data using Linux tools.",
      ],
    },
    {
      company: "Kujtesa",
      role: "Call Centre Technical Support",
      period: "Aug 2016 - Mar 2017",
      achievements: [
        "Provided technical support for internet, IPTV, and VOIP services.",
        "Diagnosed and resolved hardware and software issues remotely.",
      ],
    },
  ];

  return (
    <ExperienceSection>
      <Container>
        <motion.div
          variants={containerVariants}
          initial="hidden"
          animate="visible"
        >
          <Title variants={itemVariants}>Work Experience</Title>
          <Timeline>
            {experiences.map((exp, index) => (
              <TimelineItem
                key={exp.company}
                variants={itemVariants}
                isLeft={index % 2 === 0}
                whileHover={{ scale: 1.02 }}
              >
                <Company>{exp.company}</Company>
                <Role>{exp.role}</Role>
                <Period>{exp.period}</Period>
                <Description>
                  {exp.achievements.map((achievement, i) => (
                    <li key={i}>{achievement}</li>
                  ))}
                </Description>
              </TimelineItem>
            ))}
          </Timeline>
        </motion.div>
      </Container>
    </ExperienceSection>
  );
};

export default Experience;
